// src/utils/api.ts
import axios from 'axios';

// Create an Axios instance pointing to your Express backend.
// In development, "/api" requests proxy to "http://localhost:4000/api"
const api = axios.create({ baseURL: '/api' });

// Automatically attach the JWT from localStorage to every request.
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('jwt');
    // Ensure headers object exists
    if (!config.headers) {
      (config as any).headers = {};
    }
    // Attach the token (if present)
    if (token) {
      (config.headers as any).Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

export default api;
