// frontend/src/components/MetricCard.tsx
import React from 'react';

interface MetricCardProps {
  title: string;
  value: string | number;
}

export function MetricCard({ title, value }: MetricCardProps) {
  return (
    <div className="product-card text-center p-3" style={{ flex: '1 1 200px', margin: '0.5rem' }}>
      <h3 className="card-title">{title}</h3>
      <p className="card-text" style={{ fontSize: '1.5rem', margin: 0 }}>
        {value}
      </p>
    </div>
  );
}
