// src/pages/Dashboard.tsx
import React, { useEffect, useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import api from '../utils/api';
import { AuthContext } from '../context/AuthContext';
import { MetricCard } from '../components/MetricCard';
import {
  LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer
} from 'recharts';

interface Metrics {
  city: string;
  population: number;
  perCapitaIncome: number;
  aqi: number;
  budget: { revenues: number; expenditures: number };
}

interface HistoryPoint { year: number; population: number; }

const Dashboard: React.FC = () => {
  const { token, logout } = useContext(AuthContext);
  const navigate          = useNavigate();

  const [metrics, setMetrics] = useState<Metrics | null>(null);
  const [history, setHistory] = useState<HistoryPoint[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError]     = useState<string | null>(null);
  console.log(' token from context:', token);
  console.log(' token from localStorage:', localStorage.getItem('jwt'));

  useEffect(() => {
    if (!token) return;

    const fetchAll = async () => {
      setLoading(true);
      try {
        // 1 Get the default city from /dashboard
        const dashRes = await api.get<{ city: string }>('/dashboard');
        const cityName = dashRes.data.city;

        // 2 In parallel, fetch metrics & history for that city
        const [metricsRes, historyRes] = await Promise.all([
          api.get<Metrics>(`/cities/${encodeURIComponent(cityName)}/metrics`),
          api.get<{ city: string; history: HistoryPoint[] }>(
            `/cities/${encodeURIComponent(cityName)}/history`
          )
        ]);

        setMetrics(metricsRes.data);
        setHistory(historyRes.data.history);
        setError(null);
      } catch (err: any) {
        console.error(err);
        // If unauthorized, force logout
        if (err.response?.status === 401) {
          logout();
          navigate('/login');
        } else {
          setError('Failed to load dashboard data');
        }
      } finally {
        setLoading(false);
      }
    };

    fetchAll();
  }, [token, logout, navigate]);

  if (loading) return <p>Loading dashboard</p>;
  if (error)   return <div className="alert alert-danger">{error}</div>;

  return (
    <div className="container" style={{ marginTop: '2rem' }}>
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2>Your Dashboard</h2>
        <button className="btn btn-secondary" onClick={() => { logout(); navigate('/login'); }}>
          Sign Out
        </button>
      </div>

      <div className="d-flex flex-wrap">
        <MetricCard title="Population"        value={metrics!.population.toLocaleString()} />
        <MetricCard title="Income Per Capita" value={`$${metrics!.perCapitaIncome.toLocaleString()}`} />
        <MetricCard title="Air Quality (AQI)" value={metrics!.aqi} />
        <MetricCard
          title="Revenues"
          value={`$${(metrics!.budget.revenues/1e6).toFixed(1)}M`}
        />
        <MetricCard
          title="Expenditures"
          value={`$${(metrics!.budget.expenditures/1e6).toFixed(1)}M`}
        />
      </div>

      <h3 className="mt-5">Population Over Time</h3>
      <ResponsiveContainer width="100%" height={300}>
        <LineChart data={history}>
          <XAxis dataKey="year" />
          <YAxis />
          <Tooltip />
          <Line type="monotone" dataKey="population" stroke="#ff9636" />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
};

export default Dashboard;
